#global-caracteristicas {
  .content-cards {
    width: 90%;
    margin: 0 auto;
    @media (min-width: 992px) {
      padding: 5rem 0 0;
    }
    .row {
      @media (min-width: 992px) {
        border-radius: 25px;
        box-shadow: 0 0 20px 0px $gray3;
      }
      .col-custom {
        @media (max-width: 767.98px) {
          margin-bottom: 1rem;
        }
        .card {
          border: none;
          border-radius: 25px;
          box-shadow: 0 0 20px 0px $gray3;
          @media (min-width: 992px) {
            border-radius: none;
            box-shadow: none;
          }
          .icon-card {
            width: 100px;
            height: 100px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            margin: -25px auto 0;
            padding: 5%;
            background-color: $white;
            box-shadow: 0px 0px 25px $gray3;
            @media (min-width: 992px) {
              width: 10vw;
              height: 10vw;;
              padding: 12%;
              margin: -20% auto 0;
            }
            &:hover {
              img {
                transform: scale(1.2);
              }
            }
            img {
              width: 100%;
              transition: all 0.3s;
            }
          }
          .card-title {
            font-family: "MyriadRoman";
            font-weight: bold;
          }
          .card-text {
            span {
              font-weight: bold;
            }
          }
        }
      }
    }
  }
}