#prices {
  background-color: $gray1;
  h2 {
    font-family: "MyriadRoman";
    font-weight: bold;
    margin-bottom: 2.5rem;
    color: $black;
    text-align: center;
    @media (min-width: 992px) {
      font-size: 3rem;
    }
  }
  p {
    color: $black;
    @media (min-width: 992px) {
      font-size: 2rem;
    }
  }
  .form-check {
    padding-left: 0;
    .form-check-input {
      width: 4rem;
      height: 1.5rem;
      float: none;
      margin: 1px 1rem;
      border: 5px solid $gray3;
      background-color: $gray3;
      background-image: url(../images/icons/circle.svg);
      @media (min-width: 992px) {
        width: 6rem;
        height: 2rem;
        margin: 3px 1rem;
      }
      &:focus {
        outline: none;
        box-shadow: none;
        border: 5px solid $gray3;
        background-image: url(../images/icons/circle.svg);
      }
      &:checked {
        background-image: url(../images/icons/circle.svg);
        background-color: $gray3;
        border: 5px solid $gray3;
      }
    }
    .form-check-label {
      font-size: 1rem;
      @media (min-width: 992px) {
        font-size: 1.5rem;
      }
    }
  }
  .card {
    background-color: $white;
    border: none;
    border-radius: 10px;
    padding: 2rem 0;
    box-shadow: 5px 5px 25px 0px grey;
    height: 100%;
    .arrow {
      padding: 1rem 0;
      width: 90%;
      background-color: $red;
      clip-path: polygon(90% 0, 0 0, 0 100%, 90% 100%, 100% 50%);
      h2 {
        font-family: "MyriadRoman";
        font-weight: bold;
        color: $white;
        margin: 0;
      }
    }
    .arrow2 {
      position: absolute;
      bottom: -15px;
      right: 5%;
      height: 30px;
      width: 8rem;
      background-color: $gray2;
      clip-path: polygon(90% 0, 0 0, 0 100%, 90% 100%, 100% 50%);
      p {
        font-family: "MyriadRoman";
        font-size: 1.2rem;
        color: $white;
        margin: 0;
      }
    }
    .arrow-gray {
      background-color: $gray2;
    }
    .card-body {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding-left: 3rem;
      padding-right: 3rem;
      .small {
        font-size: 1rem;
      }
      .price {
        font-family: "MyriadRoman";
        font-size: 3rem;
        font-weight: bold;
        margin: 0;
        span {
          font-size: 1.5rem;
          font-weight: 300;
        }
      }
      hr {
        margin-top: 0;
        background-color: $gray3;
        opacity: 1;
      }
      .form-select {
        border-color: $gray3;
        background-image: url(../images/icons/angle-select.svg);
        cursor: pointer;
        &:focus {
          outline: none;
          box-shadow: none;
          border-color: $gray3;
        }
      }
      .alert {
        display: flex;
        align-items: center;
        padding: 0;
        text-align: left;
        margin-bottom: 0.5rem;
        .check {
          position: relative;
          width: 30px;
          height: 30px;
          border-radius: 0.25rem;
          display: inline-table;
          &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 15px;
            height: 8px;
            border: 3px solid $red;
            margin-left: 8px;
            border-right: 0;
            border-top: 0;
            margin-top: 8px;
            transform: rotate(-45deg);
          }
        }
        .text {
          padding-left: 1rem;
          font-size: 1rem;
          line-height: 1;
        }
      }
      a.btn {
        font-size: 1.5rem;
        font-weight: 300;
        padding: 1rem 3rem;
      }
    }
  }
}