#welcome {
  position: relative;
  background-image: url(../images/bg-welcome-int-1.png);
  background-position: bottom center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: calc(75px + 5rem);
  padding-bottom: 1rem;
  @media (min-width: 992px) {
    padding-top: calc(100px + 2rem);
  }
  @media (min-width: 1600px) {
    padding-top: calc(100px + 5rem);
  }
  .title {
    font-family: "MyriadRoman";
    font-weight: bold;
    color: $white;
    text-align: center;
    @media (min-width: 992px) {
      font-size: 3rem;
    }
    span {
      color: $red;
      font-style: italic;
    }
  }
  .access-player {
    position: relative;
    width: 80%;
    margin: 2rem auto;
    padding: 0.5rem;
    background-color: $white;
    border-radius: 5px;
    button {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 100px;
      height: 100px;
      border: none;
      border-radius: 50px;
      background-color: rgba($color: $gray2, $alpha: 0.6);
      transform: translate(-50%, -50%);
      &:hover {
        img {
          transform: scale(1.3);
          filter: drop-shadow(3px 3px 3px $gray2);
        }
      }
      div {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 70px;
        height: 70px;
        border: 2px solid $white;
        border-radius: 50px;
        transform: translate(-50%, -50%);
        display: flex;
        align-items: center;
        justify-content: center;
        img {
          margin-left: 5px;
          width: 40px;
          transition: all 0.3s;
        }
      }
    }
  }
  a.btn {
    font-size: 1.5rem;
    font-weight: 300;
    padding: 1rem 3rem;
    @media (max-width: 767.98px) {
      margin-bottom: 1rem;
    }
  }
  ul {
    margin-top: 1rem;
    margin-bottom: 0rem;
    padding: 0;
    text-align: center;
    li {
      display: inline-block;
      a {
        color: $black;
        padding: 1rem;
        transition: all 0.3s;
        @media (min-width: 992px) {
          padding: 1rem 3rem;
        }
        &:hover {
          color: $red;
        }
        img {
          margin-right: 0.3rem;
          vertical-align: text-top;
          height: 16px;
        }
      }
    }
  }
}