.modal-player {
  .modal-dialog {
    .modal-content {
      background-color: transparent;
      border: none;
      .modal-header {
        border: none;
        padding-left: 0;
        padding-right: 0;
        button {
          background: none;
          border: none;
          padding: 0;
          img {
            width: 50px;
          }
        }
      }
      .modal-body {
        position: relative;
        padding-bottom: 56.25%;
        height: 0;
        #player-modal {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}